import React, { Component } from "react";
import TodoItem from "./TodoItem";
import TodoStore from "../stores/TodoStore";
import TodoModel from "../stores/TodoModel";

import { observer } from "mobx-react";

@observer
class TodoItems extends Component {
  showall = () => {
    document.getElementById("ActiveJobs").style.display = "";
    document.getElementById("DeactiveJobs").style.display = "";
  };
  showact = () => {
    document.getElementById("ActiveJobs").style.display = "";
    document.getElementById("DeactiveJobs").style.display = "none";
  };
  showdeact = () => {
    document.getElementById("ActiveJobs").style.display = "none";
    document.getElementById("DeactiveJobs").style.display = "";
  };

  onDestroyCompleted = () => {
    TodoStore.todos.map((todo) => {
      if (todo.completed == true) {
        if (todo.remove != true) {
          todo.remove= true
          document.getElementById(todo.id).style.display='none'
          TodoStore.removetask();
          TodoStore.removeclosetask();
        }
      }
    });
  };

  render() {
    return (
      <section className="main">
        <ul className="todo-list">
          <li className="TopLinks">
            <input
              type="button"
              value="ALL"
              className="TopLinksBtn"
              onClick={this.showall}
            />
            <input
              type="button"
              value="Remaining"
              className="TopLinksBtn"
              onClick={this.showact}
            />
            <input
              type="button"
              value="Completed"
              className="TopLinksBtn"
              onClick={this.showdeact}
            />
            <input
              type="button"
              value="RemoveCompleted"
              className="TopLinksBtnRed"
              onClick={this.onDestroyCompleted}
            />
          </li>
        </ul>
        <ul className="todo-list">
          <div id="ActiveJobs">
            {TodoStore.todos.map((todo) => {
              if (todo.completed == false) {
                return <TodoItem todo={todo} />;
              }
            })}
          </div>
          <div id="DeactiveJobs">
            {TodoStore.todos.map((todo) => {
              if (todo.completed == true) {
                return <TodoItem todo={todo} />;
              }
            })}
          </div>
          <li className="footer">
            All : {TodoStore.all_tasks} | Remain:{" "}
            {TodoStore.all_tasks - TodoStore.cl_tasks} | Done :{" "}
            {TodoStore.cl_tasks}{" "}
          </li>
        </ul>
      </section>
    );
  }
}

export default TodoItems;



