import React, { Component } from "react";
import { observer } from "mobx-react";
import TodoStore from "../stores/TodoStore";

@observer
class TodoItem extends Component {
  onDestroy = () => {
    this.props.todo.destroy();
    TodoStore.removetask();
    if (this.props.todo.completed == true) {
      TodoStore.removeclosetask();
    } else {
      TodoStore.removeopentask();
    }
  };

  onToggle = () => {
    this.props.todo.toggle();
    if (this.props.todo.completed == true) {
      TodoStore.removeopentask();
      TodoStore.addclosetask();
    } else {
      TodoStore.removeclosetask();
      TodoStore.addopentask();
    }
  };

  render() {
    const { todo } = this.props;
    return (
      <li className={todo.completed ? "completed" : ""} id={todo.id}>
        <div className="view">
          <input
            onChange={this.onToggle}
            type="checkbox"
            className="toggle"
            value="on"
            checked={todo.completed}
          />
          <label>{todo.title}</label>
          <button onClick={this.onDestroy} className="destroy" />
        </div>
      </li>
    );
  }
}

export default TodoItem;



