import { observable , action} from "mobx";
import TodoModel from './TodoModel'

class TodoStore {
    @observable todos = []
    LastID = 0
    all_tasks = 0
    cl_tasks = 0
    op_tasks = 0
    @observable cl_tasks
    @observable op_tasks
    @observable all_tasks

    newtask(){
        this.all_tasks++;
        this.op_tasks++;
    };
    addopentask(){
        this.op_tasks++;
    };
    addclosetask(){
        this.cl_tasks++;
    };
    removetask(){
        this.all_tasks--;
    };
    removeopentask(){
        this.op_tasks--;
    };
    removeclosetask(){
        this.cl_tasks--;
    };

    @action
    addTodo(title){
        this.todos.push(new TodoModel(this, title, false, this.LastID ++, false))
        this.newtask()
    }
    
}

const store = new TodoStore()
export default store


